  margin: 0;
  padding-left: 0;
}

.link {
  text-decoration: none;
}

.list {
  list-style: none;
}

.btn {
  height: 34px;
  text-align: center;
  cursor: pointer;
  color: #fff;
  border: none;
  border-radius: 4px;
  padding-top: 7px;
  padding-bottom: 7px;
  font-family: inherit;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.5;
  display: block;
}

.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.no-scroll {
  overflow: hidden;
}

input:focus, textarea:focus {
  outline: 1px solid #d8e2ef;
}

select {
  vertical-align: middle;
  -webkit-appearance: none;
  -ms-appearance: none;
  appearance: none;
  background: url("select.f194d0aa.svg") calc(100% - 14px) no-repeat;
  outline: 1px solid #d8e2ef;
}

option:active {
  border: 1px solid #d8e2ef;
  outline: 1px solid #d8e2ef;
  padding-right: 8px;
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.calendar {
  width: 248px;
  height: 273px;
  cursor: pointer;
  background: #fff;
