}

.days-list li {
  color: var(--modal-main-text);
  font-size: 12px;
  font-weight: 700;
  line-height: 1.66;
}

.dates-list {
  text-align: center;
  grid-template-columns: repeat(7, 1fr);
  justify-items: center;
  gap: 2px;
  display: grid;
}

.dates-list li {
  width: 28px;
  height: 28px;
  text-align: center;
  color: var(--modal-main-text);
  align-items: center;
  justify-items: center;
  font-size: 12px;
  line-height: 1.66;
  display: grid;
}

.dates-list .pre, .dates-list .next {
  color: #9aa6b5;
}

.dates-list .active-date {
  color: #fff;
  background-color: #fd5000;
  border-radius: 50%;
}

.dates-list .hover-date {
  background-color: #d8e2ef;
  border-radius: 50%;
}

::-webkit-scrollbar {
  width: 0;
  background: none;
}

.overlay {
  opacity: 0;
  visibility: hidden;
  z-index: 20;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .7);
  transition: opacity .5s cubic-bezier(.4, 0, .2, 1), visibility .5s cubic-bezier(.4, 0, .2, 1);
  display: block;
  position: fixed;
  top: 0;
}

.modal {
  opacity: 0;
  visibility: hidden;
  z-index: 30;
  width: 1208px;
  height: 748px;
  background-color: var(--modal-bg-color);
  border: 2px solid var(--modal-bg-color);
  padding: 57px 36px;
  font-family: Poppins, sans-serif;
  transition: transform .5s cubic-bezier(.4, 0, .2, 1), min-height .5s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 73px;
  left: 50%;
  overflow: scroll;
  transform: translateX(-50%);
}

.modal .modal-close {
  width: 40px;
  height: 40px;
  color: #5e6e82;
  background: var(--modal-bg-color);
  border: none;
  justify-content: center;
  align-items: center;
  transition: border .25s cubic-bezier(.4, 0, .2, 1), color .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: absolute;
  top: 8px;
  right: 8px;
}

.modal .modal-close:hover, .modal .modal-close:focus {
  color: #fd5000;
}

.modal .close-icon {
  fill: currentColor;
}

.modal.active, .overlay.active {
  opacity: 1;
  visibility: visible;
}

